.App {
  text-align: center;
  overflow: hidden;
  z-index: 1;
}

.App-logo {
  height: 15vmin;
  pointer-events: none;
  position: absolute;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: Login-hover infinite 30s linear;
  }
}

.App-main {
  height: 10vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: left;
  font-size: calc(10px + 2vmin);
  justify-content: flex-start;
}

.App-main {
  border-bottom: 1px solid #d9dadc;
  overflow-x: hidden;
  margin-bottom: 30px;
  background-color: rgba(255, 0, 0, 0);
  height: 90vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  justify-content: flex-start;
}

.App-link {
  color: #61dafb;
}

.main-wrapper {
  width: 100%;
  height: 95%;
  padding-bottom: 5%;
  margin: auto;
  box-sizing: border-box;
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
}

.Search {
  padding: 30px;
  margin: auto;
  max-width: 800px;
  color: black;
  padding-bottom: 50px;
}
.ant-card {
  overflow: scroll;
}

.btn--loginApp-link {
  font-size: 50px;
  margin-top: 130px;
  background-color: none;
  border-radius: 10px;
  padding: 2px;
  color: #000000;
  font-family: "Roboto", sans-serif;
  z-index: 1;
}

.btn--loginApp-link:hover {
  color: #1e1b96;
}
.btn--loginApp-link:active {
  color: #ffffff;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes Login-hover {
  0% {
    -webkit-transform: translate(5%, 0%);
    transform: translate(75%, 0%) rotate(0deg);
  }
  25% {
    -webkit-transform: translate(-10%, 100%);
    transform: translate(-200%, 500%) rotate(90deg);
  }
  50% {
    -webkit-transform: translate(-5%, 200%);
    transform: translate(-100%, 1000%) rotate(180deg);
  }
  75% {
    -webkit-transform: translate(10%, 100%);
    transform: translate(200%, 500%) rotate(270deg);
  }
  100% {
    -webkit-transform: translate(5%, 0%);
    transform: translate(75%, 0%) rotate(360deg);
  }
}

#particles-js {
  position: absolute;
  width: 100%;
  height: 100vh;
  background-color: #ffffff;
  /*background-color: #1dbfaf;*/
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  z-index: -2;
}

#refresh_btn {
  width: 100%;
  float: left;
  margin: auto;
  max-width: 150px;
  max-height: 150px;
}

#refresh_btn_div {
  float: left;
  width: 20%;
}
#currentPlayer {
  height: 65%;
  padding-top: 25px;
}

canvas {
